.like-container {
  display: flex;
  flex-direction: column; /* Stack heart and count vertically */
  align-items: center;
  margin-top: 10px;
}

.like-button {
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
}

.heart-icon {
  width: 32px;
  height: 32px;
  fill: white;
  stroke: gray;
  stroke-width: 2;
  transition: fill 0.3s ease;
}

.like-button.liked .heart-icon {
  fill: red;
  stroke: red;
}

.like-count {
  margin-top: 5px;
  font-size: 14px;
  color: #333;
  text-align: center;
}
